    </van-popup>
    <audio
      ref="audio"
      :src="audioSrc"
      @timeupdate="timeupdate"
      @ended="ended"
      @canplay="canplay"
      @durationchange="durationchange"
    >
      <source :src="audioSrc" type="audio/ogg" />
      <source :src="audioSrc" type="audio/mpeg" />您的浏览器不支持 audio 元素。
    </audio>
